
*{
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

nav {
  background: #06b6d4;
  height: 4rem;
  margin-bottom: 1rem;
  position: static;

  .container {
    width: 100%;
    height: 100%;

    margin: 0 auto;
    padding: 0 3rem;

    display: flex;
    align-items: center;

    div {
      display: flex;
      align-items: center;
      flex-grow: 1;

      svg {
        height: 2rem;
        width: 2rem;
        margin-right: 0.3rem;
        color: #0ea5e9;
      }

    }

  
    
  }
}
